.page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: var(--background, #f4f6f8);
}

.form-wrapper {
  background: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
}

.form-wrapper h2 {
  margin-bottom: 20px;
  text-align: center;
}

.note {
  margin-top: 10px;
  text-align: center;
  font-size: 0.9rem;
}
